<template>
  <el-row style="min-height: 800px; width: 1200px; margin: 30px auto; text-align: left">
      <el-skeleton style="width: 240px" :loading="dataReqStatus===0" animated>
        <!-- 骨架 -->
        <template slot="template">
          <el-row style="margin-top: 15px">
            <el-col :span="8">
              <el-skeleton-item variant="image" style="width: 450px; height: 450px"/>
            </el-col>
            <el-col :span="16">
              <el-skeleton-item variant="h1" style="margin-left: 400px;width: 500px;margin-bottom: 30px" />
              <el-skeleton-item variant="h3" style="margin-left: 400px;width: 600px;margin-bottom: 10px" />
              <el-skeleton-item variant="h3" style="margin-left: 400px;width: 600px;margin-bottom: 10px" />
              <el-skeleton-item variant="h3" style="margin-left: 400px;width: 600px;margin-bottom: 10px" />
              <el-skeleton-item variant="h3" style="margin-left: 400px;width: 600px;margin-bottom: 10px" />
              <el-skeleton-item variant="h3" style="margin-left: 400px;width: 600px;margin-bottom: 10px" />
              <el-skeleton-item variant="h3" style="margin-left: 400px;width: 600px;margin-bottom: 10px" />
              <el-skeleton-item variant="h3" style="margin-left: 400px;width: 600px;margin-bottom: 10px" />
              <el-skeleton-item variant="h3" style="margin-left: 400px;width: 600px;margin-bottom: 10px" />
              <el-skeleton-item variant="h3" style="margin-left: 400px;width: 600px;margin-bottom: 30px" />
              <el-row>
                <el-col :span="4">
                  <el-skeleton-item variant="image" style="margin-left: 400px;width: 100px; height: 100px"/>
                </el-col>
                <el-col :span="4">
                  <el-skeleton-item variant="image" style="margin-left: 500px;width: 100px; height: 100px"/>
                </el-col>
                <el-col :span="4">
                  <el-skeleton-item variant="image" style="margin-left: 600px;width: 100px; height: 100px"/>
                </el-col>
                <el-col :span="4">
                  <el-skeleton-item variant="image" style="margin-left: 700px;width: 100px; height: 100px"/>
                </el-col>
                <el-col :span="4">
                  <el-skeleton-item variant="image" style="margin-left: 800px;width: 100px; height: 100px"/>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
          <el-row style="margin-top: 30px;height: 1000px">
            <el-skeleton-item variant="image" style="width: 1200px; height: 500px"/>
          </el-row>
        </template>

        <!-- 实体 -->
        <template>
          <!-- 产品简介 -->.
          <el-row style="min-height: 450px;width: 1200px">
            <!-- 异常请求视图 -->
            <el-row v-if="dataReqStatus===-1">
              <el-empty :description="dataReqErr" style="width: 1200px;margin-top: 100px"></el-empty>
            </el-row>

            <!-- 正常请求视图 -->
            <el-row style="width: 100%" v-else>
              <el-col :span="10">
                <img :src="data.images[imgIdx]" :alt="`img_${imgIdx}`" width="450" height="450">
              </el-col>
              <el-col :span="14">
                <el-row style="margin-top: 0;margin-bottom: 30px;font-size: 28px;height:40px;font-weight: bold">{{ data.title }}</el-row>
                <el-row style="color: gray;padding-right: 50px;font-size: 18px;height: 240px;overflow-y: scroll">{{ data.description }}</el-row>
                <el-row style="height: 140px">
                  <img class="product-img-box" v-for="(src, idx) in data.images" :key="idx" :src="`../../${src}`" :alt="`img_${idx}`" @mouseenter="imgIdx=idx" width="100" height="100">
                </el-row>
              </el-col>
            </el-row>
          </el-row>

          <!-- 产品介绍大图 -->
          <el-row v-if="dataReqStatus===1" style="margin-top: 30px;min-height: 500px;width: 1200px;background-color: white">
            <img :src="`../../${data.img_detail}`" alt="" width="1200">
          </el-row>
        </template>
      </el-skeleton>
    </el-row>
</template>

<script>
export default {
  name: 'detail',
  data() {
    return {
      dataReqStatus: 0, // 0=加载中, 1=成功, -1=异常
      dataReqErr: '',
      data: {
        id: 0,
        title: '',
        description: '',
        images: []
      },
      imgIdx: 0
    }
  },
  methods: {
    // 获取商品详情
    async getProductDetail() {
      this.dataReqStatus = 0
      const productId = parseInt(this.$route.query.id)
      if (isNaN(productId)) {
        this.dataReqErr = '产品id异常'
        this.dataReqStatus = -1
        return
      }

      try {
        const res = await this.$http.get(`/api/getProductDetail?id=${productId}`, {})
        const data = res.data.detail
        // this.data = {
        //   id: res.data.detail.id,
        //   title: res.data.detail.title,
        //   description: 'WQ(D)-L1型污水污物潜水电泵(以下简称电泵)，执行GB/T24674标准。电泵由水泵、密封和电动机三部分组成，电动机位于电泵上部，为单相或三相异步电动机；水泵位于电泵下部，为流道式叶轮-蜗壳结构；水泵与电机之间采用机械密封加骨架油封（0.55-1.5）/机械密封（2.2-7.5）作动密封，各固定止口密封处采用"O"形耐油橡胶密封圈作静密封，确保电泵的可靠性。电泵转子轴采用不锈钢（0.55-1.5）/摩擦焊接轴（2.2-7.5）材质，确保轴有更可靠的机械强度，可有效提高轴的耐磨性和耐腐蚀性，同时也便于叶轮的维修拆卸。',
        //   images: [
        //     'https://www.shimge.com/out/upload/2017/03/16/148963613617490tex8.png',
        //     'https://www.shimge.com/out/upload/2017/03/16/14896351212339q4zdm.png',
        //     'https://www.shimge.com/out/upload/2017/03/16/14896313058211g8gmg.png',
        //     'https://www.shimge.com/out/upload/2017/03/16/1489630588908r8ff4.png',
        //     'https://www.shimge.com/out/upload/2017/03/16/14896301200207y34b9.png'
        //   ]
        // }
        data.images = [data.img_cover]
        if (data.img_cover2) {
          data.images.push(data.img_cover2)
        }
        if (data.img_cover3) {
          data.images.push(data.img_cover3)
        }
        if (data.img_cover4) {
          data.images.push(data.img_cover4)
        }
        if (data.img_cover5) {
          data.images.push(data.img_cover5)
        }
        this.data = data
        this.dataReqStatus = 1
      } catch (err) {
        this.dataReqErr = err
        this.dataReqStatus = -1
      }
    },

    sleepMs(ms) {
      return new Promise((resolve) => {
        setTimeout(resolve, ms);
      });
    }
  },
  created() {
  },
  activated() {
    this.getProductDetail()
  }
}
</script>

<style scoped>
  ::-webkit-scrollbar {/*滚动条整体样式*/
    width: 5px;     /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
  }
  ::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    background: #535353;
  }
  ::-webkit-scrollbar-track {/*滚动条里面轨道*/
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    border-radius: 10px;
    background: #EDEDED;
  }
  .product-img-box {
    margin: 40px 40px 0 0;
    border: 1px solid rgba(200, 200, 200, 0.3);
  }
  .product-img-box:hover {
    cursor: pointer;
    border: 2px solid var(--second-color);
  }
</style>
